html,
body,
.all-wrapper {
  height: 100%;
}

body {
  font-size: 14px;
  font-weight: 400;
  font-family: "Open Sans", sans-serif;
  line-height: 22px;
  color: #666666;
}

.site-header {
  height: 100px;
  padding: 0 100px;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 9999;
  border-bottom: 1px solid #dbdbdb;
  position: absolute;
  transition: all .3s ease-in-out;
  background: #fff;
}

.logo {
  float: left;
  margin-top: 34px;
  background: url(../img/logo.png) center center no-repeat;
  background-size: contain;
}

.logo img {
  visibility: hidden;
}

.menu-side {
  float: right;
}

.menu-side .tel-side {
  display: inline-block;
  vertical-align: top;
  margin-left: 40px;
  margin-top: 30px;
}

.menu-side .tel {
  font-size: 30px;
  font-family: "cedra_4fwide", sans-serif;
  font-weight: 300;
  text-decoration: none;
  display: block;
  color: #2993b2;
}

.menu-side .graph {
  color: #666;
  font: 300 14px "cedra_4fwide", sans-serif;
  display: block;
  text-align: center;
  margin-top: 5px;
}

.main-menu {
  list-style-type: none;
  margin: 39px 0 0;
  padding: 0;
  display: inline-block;
  vertical-align: top;
}

.main-menu > li {
  display: inline-block;
  padding: 0 30px;
  position: relative;
}

.main-menu > li::after {
  content: '';
  height: 80%;
  position: absolute;
  width: 2px;
  top: 50%;
  right: 0;
  background: #747474;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.main-menu > li a {
  font-size: 13px;
  text-transform: uppercase;
  text-decoration: none;
  color: #666666;
  border-bottom: 2px solid transparent;
  padding-bottom: 2px;
  transition: all .3s ease-in-out;
  font-family: "cedra_4fwide", sans-serif;
}

.main-menu > li:hover > a {
  color: #ee0d17;
  border-color: #ee0d17;
}

.main-menu > li.current-menu-item > a {
  color: #ee0d17;
  border-color: #ee0d17;
}

.main-menu > li:last-child::after {
  display: none;
}

.site-footer {
  height: 36px;
  padding: 0 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #0d0d0d;
  border-color: transparent;
}

.site-footer p {
  margin: 10px 0 0;
  font: 400 11px "Open Sans", sans-serif;
  color: #333333;
}

.site-footer .copyright {
  float: left;
}

.site-footer .madein {
  float: right;
}

.site-footer .madein a {
  color: #2993b2;
}

.black__header .logo {
  background: url(../img/home-logo.png) center center no-repeat;
}

.black__header .site-header {
  position: absolute;
  background: rgba(0, 0, 0, 0.75);
  border-color: transparent;
}

.black__header .main-menu a {
  color: #fff;
}

.black__header .main-menu li::after {
  background: #fff;
}

.black__header .tel {
  color: #2a93b1;
}

.black__header .graph {
  color: #fff;
}

.black__header .menu-item-has-children .sub-menu {
  background: rgba(0, 0, 0, 0.7);
}

.home-slider .slick-list,
.home-slider .slick-track,
.home-slider__item {
  height: 100%;
}

.home-slider__item {
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}

.home-slider__inner {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.home-slider__container {
  max-width: 1140px;
  color: #fff;
  text-align: center;
}

.home-slider__title {
  font: 400 48px "cedra_4fwide", sans-serif;
  margin: 0 0 20px;
}

.home-slider__subheading {
  font: 400 21px "Open Sans", sans-serif;
  margin: 20px 0 40px;
}

.home-slider__list {
  list-style-type: none;
  margin: 0 0 50px;
  padding: 0;
  text-align: center;
}

.home-slider__list li {
  display: inline-block;
  padding-left: 60px;
  text-align: left;
  font: 14px/22px "Open Sans", sans-serif;
  background-position: left center;
  background-repeat: no-repeat;
  vertical-align: top;
}

.home-slider__list li:nth-child(2) {
  margin: 0 80px;
}

.home-slider .slick-arrow {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 100;
  font-size: 0px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  width: 21px;
  height: 11px;
  border: none;
  background-color: transparent;
  outline: 0;
  cursor: pointer;
  opacity: .4;
  transition: all .3s ease-in-out;
}

.home-slider .slick-arrow:hover {
  opacity: 1;
}

.home-slider .slick-arrow.slick-prev {
  left: 30px;
  background-image: url(../img/arr-l.png);
}

.home-slider .slick-arrow.slick-next {
  right: 30px;
  background-image: url(../img/arr-r.png);
}

.main-content {
  overflow: hidden;
  background-color: #f5f5f5;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
  height: 100%;
  position: relative;
}

.main-content__inner {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
