  background: #f5f5f5;
}

.main-content__col {
  width: 48.5%;
  max-height: 100%;
  overflow: auto;
  position: relative;
}

.main-content__col.img-col {
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.main-content__col img {
  display: block;
  max-width: 100%;
  height: auto;
}

.main-content__text {
  padding-top: 40px;
  padding-bottom: 200px;
}

.main-content__text h1 {
  font: 300 35px "cedra_4fwide", sans-serif;
  color: #2993b2;
  text-transform: uppercase;
  margin: 0 0 10px;
}

.main-content__text h2 {
  font: 600 18px "Open Sans", sans-serif;
  color: #404040;
  margin-top: 0;
}

.main-content__text--right {
  padding-right: 100px;
}

.main-content__text--left {
  padding-left: 100px;
  padding-right: 20px;
}

.ps-container > .ps-scrollbar-y-rail > .ps-scrollbar-y {
